import { Component, OnInit, ViewEncapsulation, AfterViewInit, AfterViewChecked, ElementRef, ViewChild, HostBinding, Renderer2, Directive} from '@angular/core';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {ListComponent, SafeHtmlPipe} from '../../list/list.component'
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { SmartSearchComponent } from '../../../layouts/smart-search/smart-search.component';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;
import { GlobalValidate } from '../../../../_services/validate.service';

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./faq-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class FAQAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
@ViewChild(ToastComponent) toast: ToastComponent;

status:string;
faqForm: FormGroup;
ticketfaqId = 0;  
QueueId = 0; 
faq = new FAQData();

msg = [];
queue = [];
category = [];
bootstrapFormValues = [];

constructor( private http: HttpClient, private _router: Router, private route:ActivatedRoute, private globalValid: GlobalValidate) {
		this.route.queryParams.filter(params => params.ticketfaqId).subscribe(params=>{
				if(params.ticketfaqId != undefined){
					this.ticketfaqId = params.ticketfaqId; 
				}
			}); 
		if(this.ticketfaqId == 0)
		{
			this.populateDropDownData();
			this.status = "Add";
		}
		else
		{
			this.populateData();
			this.status = "Update";	
		}
		
	}
	
    ngOnInit() {
			this.faqForm = new FormGroup({	
				'faqserviceTicketqueueId' : new FormControl('', [ Validators.required]),
				'ticketfaqFaqserviceId' : new FormControl('', [ Validators.required]),
				'ticketfaqQuestion' : new FormControl('', [ Validators.required]),
				'ticketfaqAnswer' : new FormControl('', [ Validators.required]),
				'ticketfaqActive' : new FormControl(''),
			});
		}
	  
 
	 get faqserviceTicketqueueId(){ return this.faqForm.get('faqserviceTicketqueueId')};
	 get ticketfaqFaqserviceId(){ return this.faqForm.get('ticketfaqFaqserviceId')};
	 get ticketfaqQuestion(){ return this.faqForm.get('ticketfaqQuestion')};
	 get ticketfaqAnswer(){ return this.faqForm.get('ticketfaqAnswer')};
	 get ticketfaqActive(){ return this.faqForm.get('ticketfaqActive')};
	 
 
	 populateCat(QueueId)
	 {
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/faq-category',
							   {'ticketqueueId':QueueId},
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.category = data['populateFaqCategory']
																	});
	 }
	 
	 
	  populateDropDownData(){
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/queue',{},
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.queue = data['populateQueue']
																	});
		  this.populateCat(this.QueueId);
	  }

		populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			  this.http.post<FAQData>(sessionStorage.getItem('requestUrl')+'/axelacrm/helpdesk/faq-data',
						JSON.stringify({ ticketfaqId :this.ticketfaqId}),
					{ headers, withCredentials: true }).subscribe(data =>{
														this.faq = data['populateData'][0];
														},
											 (err) => {
													console.log("ERROR: "+err);
											 },
											 () => {
													this.populateDropDownData();			                                    
											 });
			}

 
		onDelete(){
		 this.faq['requestType'] = 'delete';
		 this.faq['ticketfaqId'] = this.ticketfaqId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/helpdesk/faq',
									JSON.stringify({ requestType : 'delete',
												   'ticketfaqId' : this.ticketfaqId }),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['errormsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.faqForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
														});
	 	}
	  
 
	
 
		 toggleAnimation(){
				   this._router.navigate(['helpdesk/faq-list']);
		 }

 
	  	onSubmit() {
			this.bootstrapFormValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFormValues.length;i++){
				this.faq[this.bootstrapFormValues[i].name]=this.bootstrapFormValues[i].value;
				if(this.bootstrapFormValues[i].name.includes('check_')){
					this.faq[this.bootstrapFormValues[i].name.split('_')[1]] = "1";
				}else{
					this.faq[this.bootstrapFormValues[i].name] = this.bootstrapFormValues[i].value;
				}
			}
			console.log(this.faq);
		  if(!this.faqForm.invalid){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
		if(this.ticketfaqId == 0){ 
			this.faq['requestType'] = 'add';
		}else{
			this.faq['requestType'] = 'update';
			this.faq['ticketfaqId'] = this.ticketfaqId+'';
		}
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/helpdesk/faq',
									JSON.stringify(this.faq),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'] == undefined){
														this.msg[0]='Error';
			 											this.msg[1]=data['errormsg'];
														}else{
														if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}
														}
			  											
			  											this.toast.showtoast(this.msg);
			  											if(this.msg[0]!='Error'){
			 											this.faqForm.reset();
														this.toggleAnimation();
														}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  }
	  }
}

		
	
export class FAQData {
	ticketfaqId: string;
	faqserviceTicketqueueId: string;
	ticketfaqFaqserviceId: string;
	ticketfaqQuestion: string;
	ticketfaqAnswer: string;
	ticketfaqActive: string='1';
	ticketfaqEntryBy: string;
	ticketfaqEntryDate: string;
	ticketfaqModifiedBy: string;
	ticketfaqModifiedDate: string;
}



